<template>
  <b-navbar toggleable="md" type="dark" variant="dark">
    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
    <b-navbar-brand href="/inici">
      <img src="../assets/logo_gi.png" 
      class="d-inline-block align-top"
      alt="logo_great_inventions">
      GREAT INVENTIONS
    </b-navbar-brand>
    
    <b-container>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
            <!--
            <b-nav-item :to="{name: 'home'}" exact>Home</b-nav-item>
            <b-nav-item :to="{name: 'contacto'}">Contacto</b-nav-item>
           -->
            <b-nav-item class="nav-link" to="/inici">Inici</b-nav-item>
            <b-nav-item class="nav-link" to="/novetats">Novetats</b-nav-item>
            <b-nav-item class="nav-link" to="/populars">Populars</b-nav-item>
            <b-nav-item class="nav-link" to="/la_teva_pagina">Usuari</b-nav-item>
          </b-navbar-nav>
        </b-collapse>
      </b-container>
      <div class="navbar-end">
        <div class="navbar-item">
          <div class="buttons">
            <!-- Check that the SDK client is not currently loading before accessing is methods -->
            <div v-if="!$auth.loading">
              <!-- show login when not authenticated -->
              <a v-if="!$auth.isAuthenticated" @click="login" class="button is-dark"><strong>Iniciar Sessió</strong></a>
              <!-- show logout when authenticated -->
              <a v-if="$auth.isAuthenticated" @click="logout" class="button is-dark"><strong>Tancar Sessió</strong></a>
            </div>
          </div>
        </div>
      </div>
  </b-navbar>
</template>


<script>
export default {
  name: 'Nav',
  methods: {
    // Log the user in
    login() {
      this.$auth.loginWithRedirect();
    },
    // Log the user out
    logout() {
      this.$auth.logout({
        returnTo: window.location.origin
      });
    }
  }
}
</script>


