import Vue from 'vue'
import VueRouter from 'vue-router'
import Inici from '../views/Inici.vue'
import { authGuard } from "../auth/authGuard";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Inici',
    component: Inici
  },
  {
      path: '/inici',
      name: 'Inici',
      component: Inici
  },
  {
    path: '/novetats',
    name: 'Novetats',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Novetats.vue')
  },
  {
    path: '/populars',
    name: 'Populars',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Populars.vue')
  },
  {
    path: '/la_teva_pagina',
    name: 'LaTevaPagina',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/LaTevaPagina.vue'),
    beforeEnter: authGuard
  }
]

//Apartat per a que la url contingui sempre el mateix text en totes les pàgines, en aquest cas "great_inventions"
const router = new VueRouter({
  mode: "history",
  base: '/great_inventions/',
  routes
})

export default router
